
	import {
		IonIcon,
		IonPage,
		IonList,
		IonCard,
		IonItem,
		IonLabel,
		IonTitle,
		IonToggle,
		IonHeader,
		IonToolbar,
		IonContent,
	} from '@ionic/vue';
	import Header from '@/components/Header.vue';
	import { defineComponent, ref } from 'vue';
	import { moon } from 'ionicons/icons';

	export default defineComponent({
		name: 'Settings',
		components: {
			Header,
			IonIcon,
			IonPage,
			IonList,
			IonCard,
			IonItem,
			IonLabel,
			IonTitle,
			IonToggle,
			IonHeader,
			IonToolbar,
			IonContent,
		},
		setup() {
			const isDarkRef = ref(false);

			const theme = localStorage.getItem('theme') ?? 'light';

			isDarkRef.value = theme == 'dark';

			const onChangeTheme = (event: any) => {
				isDarkRef.value = event.detail.checked;

				localStorage.setItem('theme', isDarkRef.value ? 'dark' : 'light');

				document.body.classList.toggle('dark', isDarkRef.value);
			};

			return {
				moon,
				isDarkRef,
				onChangeTheme
			};
		}
	});
